<template>
  <div>
    <Layout-box one="直播间修改商品">
      <el-form :model="addForm" size="small">
        <el-form-item label="选择商品：">
          <span>{{ addForm.goodsData.name }}</span>
        </el-form-item>
        <el-form-item v-if="addForm.goodsData.skus.length" label="规格：">
          <el-table :data="addForm.goodsData.skus">
            <el-table-column v-for="(item, i) in addForm.goodsData.items" :key="i" :label="item.keyName">
              <template slot-scope="scope">
                <el-input v-model="scope.row.attrSymbolNo" />
              </template>
            </el-table-column>
            <el-table-column label="原价">
              <template slot-scope="scope">
                <el-input v-model="scope.row.originalPrice" />
              </template>
            </el-table-column>
            <el-table-column label="现价">
              <template slot-scope="scope">
                <el-input v-model="scope.row.price" />
              </template>
            </el-table-column>
            <el-table-column label="库存">
              <template slot-scope="scope">
                <el-input v-model="scope.row.stock" />
              </template>
            </el-table-column>
          </el-table>
        </el-form-item>
        <el-form-item>
          <el-button size="small" @click="onSubmit">提交</el-button>
        </el-form-item>
      </el-form>
    </Layout-box>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox'

export default {
  name: 'liveRoomAddGoods',

  components: {
    LayoutBox
  },

  data () {
    return {
      addForm: {
        produId: '',
        goodsData: {
          skus: []
        },
      },
    }
  },

  created () {
    this.addForm.goodsData = JSON.parse(this.$route.query.data)
  },

  methods: {
    // 点击提交
    onSubmit () {
      const data = {
        itemSkus: this.addForm.goodsData.skus,
        produId: this.addForm.goodsData.id
      }
      this.apiInsertAnchorProduct(data)
    },
    
    // 添加直播商品
    async apiInsertAnchorProduct (data) {
      const res = await this.$http.post(this.$api.updateAnchorProduct, data)
      if (res.data.success) {
        this.$message.success('保存成功')
        this.$router.go(-1)
      } else {
        this.$message.error(res.data.msg)
      }
    }
  }, 

}
</script>

<style lang="scss" scoped>

</style>
